.header{
    position: sticky;
    position: -webkit-sticky;
    height: 85px;
    width: 100%;
    background-color: white;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding: 15px 20px;
    z-index:100;
    top:0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);

}
.header > .left{
    width: 20%;
    display: flex;
    justify-content: center;
    margin-right: 20px;
}
.header > .middle{
    display: flex;
    justify-content: center;
    width:60%;
}
#searchBox{
    /* color: blueviolet; 
