.sideBarRow{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    margin: 0px;
    overflow: hidden;
    border-radius: 10px;
}
.sideBarRow:hover{
    background-color: lightgray;
    border-radius: 10px;
}
.sideBarRow>h4{
    margin: 0px;
    margin-left: 20px;
    font-weight: 600;
}
.sideBarRow>.icon{
    font-size: xx-large;
    color: #ca212e;
}